.avatar-img{
  height: 250px;
  padding-top: 5em;
}

.circular {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  margin: auto;
}

.circular-resume {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  margin: auto;
}

.circular-resume img {
  min-width: 50%;
  min-height: 50%;
  width: auto;
  position: absolute;
  left: 50%;
  top: 60%;
  height: 250px;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.circular-contact {
  width: 50%;
  height: 50%;
  border-radius: 20%;
  position: relative;
  overflow: hidden;
  margin: auto;
  background: #27221F;
}

.circular-contact img {
  min-width: 130%;
  min-height: 130%;
  position: absolute;
  left: 208%;
  top: 200%;
  -webkit-transform: translate(-120%, -120%);
  -moz-transform: translate(-100%, -100%);
  -ms-transform: translate(-100%, -100%);
  transform: translate(-160%, -160%);
}


.circular img {
  min-width: 100%;
  min-height: 100%;
  width: auto;
  position: absolute;
  left: 15%;
  top: 50%;
  height: 250px;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.header-color{
  background: #373B44;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #4286f4, #373B44);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #4286f4, #373B44); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid{
  background: #373B44;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #4286f4, #373B44);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #4286f4, #373B44); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  position: fixed;
  top: auto;
  left: auto;
  width: 100%;
  height: 100%;
}

.projects-grid{
  display: flex;
}

.contact-body{
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em; 
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2 {
  font-family: 'Anton', sans-serif;
}

.contact-grid p {
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}

.contact-grid hr{
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 60px;
  padding-right: 5rem;

}

.resume-right-col{
  background: #27221F;
  color: white;
  padding: 2em;

}

.banner-text{
background-color: black;
opacity: .8;
width: 75%;
margin: auto;
border-radius: 10px;
}

.banner-text h1{
  font-size: 56px;
  font-weight: bold;
  color: white;
  margin: 0;
}

.banner-text hr{
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links {
 display: flex;
 justify-content: space-between;
 width: 50%;
 margin: auto;
}

.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}


.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
